<template>
  <div class="inedx">
    <h1>场宁阅读器</h1>
    <div class="search">
      <el-input
        placeholder="搜索或输入网址"
        v-model="url"
        clearable
        prefix-icon="el-icon-search"
        v-on:change="search(url)"
      />
    </div>
    <el-card class="historys" shadow="never" v-if="historys.length">
      <div slot="header" class="not-importent">
        <span>历史记录</span>
      </div>
      <div
        v-for="(history, index) in historys"
        :key="history.time"
        class="history"
      >
        <el-divider v-if="index"></el-divider>
        <el-link @click="openHistoryDocument(index)">{{ history.title }}</el-link>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      url: "",
      historys: [],
    };
  },
  activated() {
    const historys = JSON.parse(
      localStorage.getItem("documentHistory") || "[]"
    );
    this.historys = historys.slice(0, 100);
  },
  methods: {
    search(url) {
      if (url) {
        this.$router.push({ path: "/document", query: { url } });
      }
    },
    openHistoryDocument(index) {
      const history = this.historys[index];
      // 清除这一条历史记录
      this.historys.splice(index, 1);
      localStorage.setItem('documentHistory', JSON.stringify(this.historys));
      // 打开文档
      this.$router.push({ path: "/document", query: { url: history.url } });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.inedx {
  margin: 15px;
}
.search {
  margin: 25px 0;
}
.history {
  margin: 10px 0;
}
</style>
