import Vue from 'vue'
import App from './App.vue'
import Index from './components/Index.vue'
import Document from './components/Document.vue'
import VueRouter from 'vue-router'
import cloudbase from "@cloudbase/js-sdk"
import { Button, Input, Card, Divider, Link, PageHeader, Image, Loading} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css'
import './assets/my-style.css'

// 应用路由插件
Vue.use(VueRouter)
// 应用UI
Vue.use(Button);
Vue.use(Input);
Vue.use(Card);
Vue.use(Divider);
Vue.use(Link);
Vue.use(PageHeader);
Vue.use(Image);
Vue.use(Loading.directive);

Vue.prototype.$loading = Loading.service;

Vue.config.productionTip = false

const routes = [
  { path: '/', component: Index },
  { path: '/index', component: Index },
  { path: '/document', component: Document , props: (route) => ({ url: route.query.url })},
]

const router = new VueRouter({
  routes // （缩写）相当于 routes: routes
})

function signIn() {
  // 链接腾讯云
  const cloud = Vue.prototype.cloud = cloudbase.init({
    // env: "xxx",
    // env: "xmrl-y0wtp", // 开发环境
    env: "xmrl-prod-jawjt", // 生产环境
  })
  const cloudAuth = Vue.prototype.cloudAuth = cloud.auth({
    persistence: 'local'
  })
  // 避免重复登录
  if (cloudAuth.hasLoginState()) {
    console.log('无需重复登录')
    return Promise.resolve()
  }
  return cloudAuth.anonymousAuthProvider()
    .signIn()
    .then((item) => {
      console.log("登录成功", item);
      return Promise.resolve()
    })
    .catch((error) => {
      console.error("登录失败", error);
      return Promise.reject();
    });
}

signIn().then(() => {
  // 链接成功后再实例化app
  new Vue({
    render: h => h(App),
    router
  }).$mount('#app')
})

