<template>
  <div class="document" v-if="document">
    <el-page-header @back="$router.back()" content="正在阅读"> </el-page-header>
    <h1>{{ document.title }}</h1>
    <div class="contents">
      <div
        class="content"
        v-for="content in document.list"
        :key="content.insideId"
      >
        <p v-if="content.type == 'text'">
          <el-link
            type="primary"
            v-if="content.ref"
            @click="$router.push({ query: { url: content.ref } })"
            >{{ content.content }}</el-link
          >
          <span v-else>{{ content.content }}</span>
        </p>
        <p v-else-if="content.type == 'img'">
          <el-image :src="content.content" />
        </p>
      </div>
    </div>
    <div class="not-importent host-tip">
      <el-link :href="document.url" target="_blank" type="info"
        >内容由{{ urlHost }}提供</el-link
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "Document",
  props: {
    url: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      document: null,
      cachedDocuments: {},
    };
  },
  computed: {
    urlHost: function () {
      if (!this.document) {
        return "";
      }
      var result = /^https?:\/\/(.+?)(?:(?:\/)|$)/i.exec(this.document.url);
      return result[1] || this.document.url;
    },
  },
  watch: {
    url: function (nVal) {
      if (nVal) {
        // 检查缓存
        const cachedDocument = this.cachedDocuments[nVal];
        if (cachedDocument) {
          this.document = cachedDocument;
          // 添加历史记录
          this.pushHistory();
          // 滚动到顶部
          scrollTo(0, 0);
        } else {
          this.loadDocument();
        }
      }
    },
  },
  created() {
    this.loadDocument();
  },
  methods: {
    loadDocument() {
      const loading = this.$loading();
      console.log(`加载：${this.url}`);
      this.cloud
        .callFunction({
          name: "loadDocument",
          data: {
            url: this.url,
          },
        })
        .then((res) => {
          this.document = this.cachedDocuments[this.url] = res.result;
          // 关闭加载遮罩
          loading.close();
          // 添加历史记录
          this.pushHistory();
          // 滚动到顶部
          scrollTo(0, 0);
        })
        .catch((err) => {
          console.error(err);
          // 关闭加载遮罩
          loading.close();
        });
    },

    pushHistory() {
      const documentHistory = JSON.parse(
        localStorage.getItem("documentHistory") || "[]"
      );
      documentHistory.unshift({
        title: this.document.title,
        url: this.document.url,
        time: Date.now(),
      });
      localStorage.setItem("documentHistory", JSON.stringify(documentHistory));
    },
  },
};
</script>

<style scoped>
.content {
  word-break: break-all;
}
.host-tip {
  text-align: center;
  margin: 30px 0;
}
</style>